import moment from 'moment'

module.exports = function (articleName) {
  let md = require('../components/articles/' + articleName + '.md')
  console.log(md)
  let mdinfoobj = JSON.parse(md.substring(md.indexOf('{'), md.indexOf('}') + 1))
  return {
    info: mdinfoobj,
    context: md,
    getDate () {
      return moment(mdinfoobj.date, 'YYYY-MM-DD')._d
    }
  }
}



