<template lang="jade">
  div.post.flex-column
    h1.title Posts
    a(v-for="postName in postIndex"
      v-link="{ path: '/post/' + postName }") {{ postName }}
</template>

<script>
  import index from '../article-index.js'
  import info from '../utilities/articleInfo'

  export default {
    data () {
      return {
        postIndex: index.post
      }
    },
    ready () {
      this.postIndex.sort(function (x, y) {
        return info(x).getDate() < info(y).getDate()
      })
    }
  }
</script>

<style lang="scss" scoped>
  .post {
    padding: 20px 20px;
  }
</style>



